@import url(https://fonts.googleapis.com/css2?family=Graduate&display=swap);
.GoodsList{margin:0;padding:0;color:#202c39;text-align:center;font-size:24px;line-height:150%;list-style:none}

.SortableList{width:100vw;min-height:100vh;display:flex;flex-direction:column;justify-content:center;align-items:center}.SortableList__buttonsSection{padding-top:18px;display:flex;flex-direction:row;grid-gap:4px;gap:4px}.SortableList__checkbox{display:none}.SortableList__checkbox:checked+.SortableList__toggler{color:#283845;background-color:#f29559}.SortableList__button{padding:4px 16px;color:#b8b08d;font-family:inherit;font-size:18px;line-height:150%;background-color:#283845;border:none;border-radius:2px}.SortableList__button:hover{color:#283845;background-color:#f29559}.SortableList__dropdown{font-family:inherit;font-size:18px;line-height:150%;color:#b8b08d;outline:none;border:none;border-radius:2px;background-color:rgba(40,56,69,0.8)}.SortableList__goodsList{flex-grow:1;display:flex;flex-direction:column;justify-content:center}

body{margin:0;font-family:"Graduate", sans-serif;background-color:#b8b08d}

